
import { Vue } from 'vue-class-component';
export default class Button extends Vue {
  public loadingStatus = false;
  handleClick() {
    if (this.loadingStatus) {
      return;
    }
    this.loadingStatus = true;
    const timer = setTimeout(() => {
      this.loadingStatus = false;
      clearTimeout(timer);
    }, 5000);
  }
}
