
import { Vue } from 'vue-class-component';
export default class Dialog extends Vue {
  public dialog1 = false;
  public dialog2 = false;
  handleClick(num: number): void {
    this[`dialog${num}`] = !this[`dialog${num}`];
  }
  handleBeforeClose(e) {
    console.log("", e);
    if (e === "cancel") {
      return;
    }
    return new Promise((resolve, reject) => {
      console.log(reject);
      setTimeout(() => {
        resolve("");
      }, 2000);
    }).catch(err => {
      console.log("", err);
    });
  }
  alert() {
    this.Dialog.alert({ title: "sync alert", message: "", closeModelOnClick: true });
  }
  asyncAlert() {
    this.Dialog.alert({ title: "async alert", message: "", closeModelOnClick: true, beforeClose: this.handleBeforeClose });
  }
  asyncConfirm() {
    this.Dialog.confirm({ title: "confirm", message: "", beforeClose: this.handleBeforeClose, textHideWhenLoading: true });
  }
}
