    return {
      params: { a: "1", b: "2" },
      action: this.getSearchAction,
      parse: (e, params = {}) => {
        const { tab } = params;
        if (tab === "mainland-china") {
          return e.cnCitiesList;
        } else if (tab === "overseas") {
          return e.intCitiesList;
        }
      }
    };
  }
  getSearchAction(params = {}) {
    console.log("", params);
    return new Promise((resolve, reject) => {
      console.log(reject);
      // setTimeout(() => {
      //   reject("");
      // }, 1000);
      setTimeout(() => {
        resolve(data);
      }, 1000);
    }).catch(err => {
      this.Toast(err);
      console.log(err, "");
    });
  }
