
import { Vue } from "vue-class-component";
// import VueGgy from "../../src/component/VueGgy";
export default class Tabs extends Vue {
  public active = 1;
  public active1 = 1;
  public active2 = 1;
  change(e: any) {
    this.Toast(`${e.value} changed`);
  }
  handleClick(e: any) {
    if (e.disabled) {
      this.Toast(`${e.value} is disabled`);
    } else {
      this.Toast(`${e.value} clicked`);
    }
  }
}
