    { value: "Georqia", disabled: true },
    { value: "Indiana", disabled: true },
    { value: "Maine", disabled: false },
    { value: "Amenda", disabled: true }
  ];
  public columns2 = [
    { value: ["Delaware", "Florida", "Georqia", "Indiana", "Maine"] },
